.dashboard {
    text-align: center;
    font-family: Arial, sans-serif;
    padding: 20px;
    background-color: #f8f9fa;
  }
  
  .dashboard-title {
    color: #ff4500;
    font-size: 28px;
    margin-bottom: 20px;
  }
  
  /* Gym Tips Section */
  .tips-section {
    background: #fff;
    padding: 15px;
    margin: 20px auto;
    border-radius: 10px;
    width: 80%;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  .tips-section h2 {
    color: #ff6347;
  }
  
  .tips-section ul {
    list-style-type: none;
    padding: 0;
  }
  
  .tips-section li {
    margin: 10px 0;
    font-weight: bold;
    color: #008000;
  }
  
  /* Exercises Section */
  .exercises-section {
    margin-top: 30px;
  }
  
  .exercise-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .exercise-card {
    border: 2px solid #ddd;
    padding: 10px;
    border-radius: 10px;
    width: 220px;
    background-color: #fff;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
    cursor: pointer;
  }
  
  .exercise-card:hover {
    transform: scale(1.1);
    box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.3);
  }
  
  .exercise-img {
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-radius: 5px;
  }
  
  .exercise-card h3 {
    margin: 10px 0 5px;
    color: #333;
  }
  
  .exercise-card p {
    color: #555;
    font-weight: bold;
  }
  
  /* Gallery Section */
  .gallery-section {
    margin-top: 30px;
  }
  
  .gallery-grid {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-top: 10px;
  }
  
  .gallery-grid img {
    width: 150px;
    height: 100px;
    border-radius: 8px;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);
  }  